import React from 'react';
import { HashRouter as Router, Routes, Route, Link, useLocation } from 'react-router-dom';
import { SurveyProvider } from './context/SurveyContext';
import SurveyForm from './components/SurveyForm';
import SurveyManager from './components/SurveyManager';
import SurveyResults from './components/SurveyResults';
import './App.css';

function Navigation() {
  const location = useLocation();
  
  return (
    <nav className="navigation">
      <Link to="/" className={location.pathname === '/' ? 'active' : ''}> </Link>
      <Link to="/manage" className={location.pathname === '/manage' ? 'active' : ''}> </Link>
      <Link to="/results" className={location.pathname === '/results' ? 'active' : ''}> </Link>
    </nav>
  );
}

function App() {
  return (
    <SurveyProvider>
      <Router>
        <div className="min-h-screen bg-gray-100">
          <header className="bg-white shadow">
            <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
              <h1 className="text-3xl font-bold text-gray-900"> </h1>
              <Navigation />
            </div>
          </header>
          <main>
            <Routes>
              <Route path="/" element={<SurveyForm surveyId="1" onComplete={() => {}} />} />
              <Route path="/manage" element={<SurveyManager />} />
              <Route path="/results" element={<SurveyResults surveyId="1" />} />
            </Routes>
          </main>
        </div>
      </Router>
    </SurveyProvider>
  );
}

export default App;
