import React, { createContext, useContext, useState, ReactNode, useEffect } from 'react';
import { Survey, SurveyResponse } from '../types/survey';
import * as XLSX from 'xlsx';

const initialSurvey: Survey = {
  id: '1',
  title: '  ',
  questions: [
    {
      id: '1',
      text: '    ?',
      type: 'text'
    },
    {
      id: '2',
      text: '   ?',
      type: 'single',
      options: ['1', '2', '3', '4']
    },
    {
      id: '3',
      text: '   ?',
      type: 'multiple',
      options: ['UI/UX', '', '', '']
    }
  ]
};

interface SurveyContextType {
  surveys: Survey[];
  responses: SurveyResponse[];
  addSurvey: (survey: Survey) => void;
  updateSurvey: (survey: Survey) => void;
  deleteSurvey: (id: string) => void;
  addResponse: (response: SurveyResponse) => void;
  getResponsesBySurveyId: (surveyId: string) => SurveyResponse[];
  exportResponsesToExcel: (surveyId: string) => void;
}

const SurveyContext = createContext<SurveyContextType | undefined>(undefined);

export const SurveyProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [surveys, setSurveys] = useState<Survey[]>(() => {
    const savedSurveys = localStorage.getItem('surveys');
    if (savedSurveys) {
      const parsedSurveys = JSON.parse(savedSurveys);
      return parsedSurveys.length > 0 ? parsedSurveys : [initialSurvey];
    }
    return [initialSurvey];
  });

  const [responses, setResponses] = useState<SurveyResponse[]>(() => {
    const savedResponses = localStorage.getItem('responses');
    return savedResponses ? JSON.parse(savedResponses) : [];
  });

  useEffect(() => {
    localStorage.setItem('surveys', JSON.stringify(surveys));
  }, [surveys]);

  useEffect(() => {
    localStorage.setItem('responses', JSON.stringify(responses));
  }, [responses]);

  const addSurvey = (survey: Survey) => {
    setSurveys(prev => [...prev, survey]);
  };

  const updateSurvey = (survey: Survey) => {
    setSurveys(prev => prev.map(s => s.id === survey.id ? survey : s));
  };

  const deleteSurvey = (id: string) => {
    setSurveys(prev => prev.filter(s => s.id !== id));
    setResponses(prev => prev.filter(r => r.surveyId !== id));
  };

  const addResponse = (response: SurveyResponse) => {
    setResponses(prev => [...prev, response]);
  };

  const getResponsesBySurveyId = (surveyId: string) => {
    return responses.filter(r => r.surveyId === surveyId);
  };

  const exportResponsesToExcel = (surveyId: string) => {
    const survey = surveys.find(s => s.id === surveyId);
    if (!survey) return;

    const surveyResponses = responses.filter(r => r.surveyId === surveyId);
    const worksheet = XLSX.utils.json_to_sheet(
      surveyResponses.map(response => {
        const rowData: Record<string, string> = {
          ' ': new Date(parseInt(response.timestamp)).toLocaleString()
        };

        Object.entries(response.answers).forEach(([questionId, answer]) => {
          const question = survey.questions.find(q => q.id === questionId);
          if (question) {
            rowData[question.text] = Array.isArray(answer) ? answer.join(', ') : String(answer);
          }
        });

        return rowData;
      })
    );
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, ' ');
    XLSX.writeFile(workbook, `${survey.title}_.xlsx`);
  };

  return (
    <SurveyContext.Provider value={{
      surveys,
      responses,
      addSurvey,
      updateSurvey,
      deleteSurvey,
      addResponse,
      getResponsesBySurveyId,
      exportResponsesToExcel
    }}>
      {children}
    </SurveyContext.Provider>
  );
};

export const useSurvey = () => {
  const context = useContext(SurveyContext);
  if (context === undefined) {
    throw new Error('useSurvey must be used within a SurveyProvider');
  }
  return context;
}; 